import { JetView } from "webix-jet";

export default class ChartView extends JetView {
	config() {
		const state = (this.State = this.getParam("state", true));

		const chart = {
			view: "chart",
			$mainView: true,
			borderless: true,
			localId: "data",
			xAxis: {},
			yAxis: {},
		};

		webix.extend(chart, state.chart, true);

		if (!chart.xAxis.template)
			chart.xAxis.template = obj => obj[obj.length - 1];

		const commonAxisSettings = {
			lines: state.chart.lines,
		};

		//save default chart line color
		if (state.chart.scaleColor)
			commonAxisSettings.color = commonAxisSettings.lineColor =
				state.chart.scaleColor;

		webix.extend(chart.xAxis, commonAxisSettings, true);
		webix.extend(chart.yAxis, commonAxisSettings, true);

		return chart;
	}

	init() {
		this.Local = this.app.getService("local");
		this.LoadData();

		this.on(this.State.$changes, "structure", (structure, old) => {
			if (old) this.UpdateStructure();
		});

		this.on(this.State.$changes, "chart", (val, o) => {
			if (o) this.refresh();
		});
	}
	/**
	 * Loads data to a chart
	 * @returns {Promise} promise that resolves when data is parsed
	 */
	LoadData() {
		return this.Local.getData().then(data => {
			this.UpdateChart(data);
		});
	}
	/**
	 * Updates chart during structure changes
	 */
	UpdateStructure() {
		const data = this.Local.getPivotData();
		this.UpdateChart(data);
	}
	/**
	 * Updates chart data
	 * @param {Array} data - array of data
	 */
	UpdateChart(data) {
		this.$$("data").clearAll();
		this.$$("data").removeAllSeries();
		this.SetSeries(data.values);
		this.$$("data").parse(data.data);
	}
	/**
	 * Prepares and sets chart series
	 * @param {Array} values - array of values for series
	 */
	SetSeries(values) {
		const type = this.State.chart.type;

		const _ = this.app.getService("locale")._;
		for (let i = 0; i < values.length; i++) {
			this.$$("data").addSeries({
				value: obj => obj[i],
				alpha: type == "area" || type == "splineArea" ? 0.7 : 1,
				color: values[i].color,
				tooltip: obj => obj[i],
				item: {
					color: values[i].color,
					borderColor: values[i].color,
				},
				line: {
					color: values[i].color,
				},
			});

			if (values[i].text != values[i].operation) {
				let text = values[i].text.split(",");
				text = text.map(t => this.Local.getField(t).value).join(", ");
				values[i].text = `${text} (${_(values[i].operation)})`;
			} //complex operation
			else values[i].text = this.Local.fixMath(values[i].operation);
		}

		this.SetLegend(values);
	}
	/**
	 * Prepares and sets chart legend
	 * @param {Array} values - array of values for legend
	 */
	SetLegend(values) {
		const legend = webix.extend(
			{
				values,
				valign: "middle",
				align: "right",
				layout: "y",
			},
			this.State.chart.legend || {},
			true
		);
		this.$$("data").define({ legend });
	}
}
