import { JetView } from "webix-jet";

export default class ChartSettings extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		return {
			view: "form",
			complexData: true,
			padding: 10,
			elements: [
				{
					name: "type",
					view: "richselect",
					value: "bar",
					options: {
						css: "webix_pivot_suggest",
						data: [
							{ id: "bar", value: _("Bar") },
							{ id: "line", value: _("Line") },
							{ id: "radar", value: _("Radar") },
							{ id: "area", value: _("Area") },
							{ id: "spline", value: _("Spline") },
							{ id: "splineArea", value: _("Spline Area") },
						],
					},
				},
				{
					name: "xAxis.title",
					view: "text",
					label: _("X axis title"),
					batch: "axis",
				},
				{
					name: "yAxis.title",
					view: "text",
					label: _("Y axis title"),
					batch: "axis",
				},
				{
					name: "scaleColor",
					view: "colorpicker",
					editable: true,
					label: _("Scale color"),
					suggest: {
						type: "colorboard",
						body: {
							width: 150,
							height: 150,
							view: "colorboard",
							palette: this.app.getService("local").getPalette(),
						},
					},
				},
				{
					name: "scale",
					view: "checkbox",
					checkValue: "logarithmic",
					uncheckValue: "linear",
					labelWidth: 0,
					labelRight: _("Logarithmic scale"),
				},
				{
					name: "yAxis.lineShape",
					view: "checkbox",
					batch: "radar",
					checkValue: "arc",
					labelWidth: 0,
					labelRight: _("Circled lines"),
				},
				{
					name: "lines",
					view: "checkbox",
					labelWidth: 0,
					labelRight: _("Lines"),
				},
			],
			on: {
				onChange: (value, oldValue, source) => {
					if (source == "user") {
						this.innerChange = true;
						this.State.chart = Object.assign({}, this.getRoot().getValues());
						this.handleVisibility();
						delete this.innerChange;
					}
				},
			},
		};
	}
	init() {
		this.State = this.getParam("state", true);

		this.on(this.State.$changes, "chart", chart => {
			if (!this.innerChange) {
				this.getRoot().setValues(chart);
				this.handleVisibility();
			}
		});
	}
	handleVisibility() {
		const form = this.getRoot();

		if (this.State.chart.type == "radar") form.showBatch("radar");
		else form.showBatch("axis");
	}
}
