import Property from "views/config/properties";

export default class ValuesProperty extends Property {
	constructor(app, name, config) {
		super(app, name, config);
		this.Local = this.app.getService("local");

		const _ = this.app.getService("locale")._;

		this.typeName = "operation";
		this.plusLabel = _("Add value");
		this.field = "values";

		this.operations = this.Local.operations;

		this.operations.map(operation => {
			operation.value = _(operation.id);
			return operation;
		});
	}
	init() {
		super.init();

		this.State = this.app.getState();

		this.on(this.State.$changes, "mode", mode => {
			this.ToggleColors(mode == "chart");
		});
	}
	GetValue() {
		const forms = this.$$("forms").getChildViews();
		const arr = [];

		forms.forEach(form => {
			const values = form.getChildViews()[0].getValues({ hidden: false });
			if (!webix.isUndefined(values.name2)) {
				//wavg
				if (values.name && values.name2) {
					values.name = [values.name, values.name2];
					delete values.name2;
				} else values.name = "";
			}
			// line must always contain the field, except complex math
			if (values.name == "") return;
			arr.push(values);
		});

		return arr;
	}
	ItemConfig(val, i) {
		const config = super.ItemConfig(val);

		if (val && !val.name) {
			//custom math
			config.splice(1, 1, {
				view: "label",
				css: "webix_pivot_complex_operation",
				name: "operation",
				label: this.Local.fixMath(val.operation),
				value: val.operation,
			});
		} else {
			let operation;
			if (val) operation = val.operation;
			else {
				const def = this.app.config.defaultOperation;
				const defIndex = Math.max(
					this.operations.findIndex(a => a.id == def),
					0
				);
				operation = this.operations[defIndex].id;
			}

			config.splice(2, 0, {
				view: "richselect",
				name: "operation",
				width: 100,
				value: operation,
				options: {
					css: "webix_pivot_suggest",
					data: this.operations,
				},
				on: {
					onChange: function(v) {
						this.$scope.SetOperation(v, this);
					},
				},
			});

			config.splice(2, 0, {
				view: "richselect",
				hidden: !val || val.operation != "wavg",
				value: val && webix.isArray(val.name) ? val.name[1] : "",
				name: "name2",
				options: {
					css: "webix_pivot_suggest",
					data: this.app.getState().fields,
				},
			});
		}

		const mini = webix.skin.$name == "mini" || webix.skin.$name == "compact";
		const palette = this.Local.getPalette();
		const color = (val && val.color) || this.Local.getValueColor(i);

		config.splice(1, 0, {
			view: "colorpicker",
			hidden: this.State.mode != "chart",
			name: "color",
			css: "webix_pivot_value_color",
			value: color,
			width: mini ? 30 : 38,
			suggest: {
				type: "colorboard",
				body: {
					width: 150,
					height: 150,
					view: "colorboard",
					palette,
				},
			},
		});

		return config;
	}
	ToggleColors(show) {
		const layout = this.$$("forms");
		const forms = layout.getChildViews();

		for (let i = 0; i < forms.length; i++) {
			const input = forms[i].getChildViews()[0].elements.color;
			if (show) input.show();
			else input.hide();
		}
	}
	FilterSuggest() {
		return true;
	}
	SetOperation(val, view) {
		const form = view.getFormView();
		if (val == "wavg") form.elements.name2.show();
		else form.elements.name2.hide();
	}
}
