import { JetView } from "webix-jet";

export default class TableView extends JetView {
	config() {
		this.Config = this.app.config; //state props
		this.Local = this.app.getService("local");
		const state = (this.State = this.getParam("state", true));

		const table = {
			view: "treetable",
			$mainView: true,
			localId: "data",
			css: "webix_data_border webix_header_border",
			select: true,
			leftSplit: state.mode == "table" ? state.structure.rows.length : 1,
			resizeColumn: true,
			borderless: true,
			columns: [],
			footer: state.datatable.footer,
		};

		webix.extend(table, state.datatable, true);

		return table;
	}

	init() {
		this.LoadData();

		this.on(this.State.$changes, "structure", (structure, old) => {
			if (old) this.UpdateStructure();
		});

		this.on(this.State.$changes, "datatable", (val, o) => {
			if (o) this.refresh();
		});

		this.on(this.State.$changes, "mode", (val, o) => {
			// [FIXME] does not rerender view via toolbar mode handler
			// if users switch between table and tree after visiting chart tab
			if (o && o != "chart") this.refresh();
		});
	}
	/**
	 * Loads data to a table
	 * @returns {Promise} promise that resolves when data is parsed
	 */
	LoadData() {
		return this.Local.getData().then(data => {
			this.UpdateTable(data);
		});
	}
	/**
	 * Updates table during structure changes
	 */
	UpdateStructure() {
		const data = this.Local.getPivotData();

		this.UpdateTable(data);
	}
	/**
	 * Updates table data
	 * @param {Array} data - array of data
	 */
	UpdateTable(data) {
		const table = this.$$("data");
		table.clearAll();
		if (this.State.mode == "table")
			table.config.leftSplit = this.State.structure.rows.length;

		table.refreshColumns(this.SetColumns(data.header, data.total, data.marks));
		table.parse(data.data);
	}
	/**
	 * Prepares table columns
	 * @param {Array} columns - array with columns
	 * @param {Array} totals - array with total values for columns
	 * @param {Array} marks - array with min/max marks for columns
	 * @returns {Array} column configurations
	 */
	SetColumns(columns, totals, marks) {
		const _ = this.app.getService("locale")._;
		const left =
			this.State.mode == "table" ? this.State.structure.rows.length : 1;

		for (let i = 0; i < columns.length; i++) {
			if (!i) {
				this.SetFirstColumn(columns[i], _);
			} else {
				if (i >= left) columns[i].sort = "int";
				else if (this.State.mode == "table") columns[i].width = 200;

				if (!columns[i].format && i >= left)
					columns[i].format = this.CellFormat;

				if (marks)
					columns[i].cssFormat = (v, row, rid, cid) => {
						const col = marks[rid - 1];
						const css = col ? col[cid - 1] : null;
						return css ? css.join(" ") : "";
					};

				const header = columns[i].header;
				for (let j = 0; j < header.length; j++) {
					let h = header[j];
					if (h) {
						if (!j && h.name == "total") {
							//rengine for total column?
							h.text = _("Total");
						} else if (j == header.length - 1) {
							h.text = this.HeaderTemplate(h, _);
						}
					}
				}
				if (totals.length) {
					columns[i].footer = this.CellFormat(totals[i]);
				}
			}
		}

		return columns;
	}
	/**
	 * Prepares first column
	 * @param {Object} column - column configuration
	 * @param {function} _ - function for translating text labels
	 */
	SetFirstColumn(column, _) {
		if (this.State.mode == "tree") {
			column.header = "";
			column.width = 300;
			column.template = (obj, common) => {
				return common.treetable(obj, common) + obj[1];
			};
		} else {
			column.header = column.header[0].text;
			column.width = 200;
		}
		if (this.State.datatable.footer) column.footer = _("Total");
	}
	/**
	 * Sets a template for column header
	 * @param {Object} line - object with header line properties
	 * @param {function} _ - function for translating text labels
	 * @returns {string} string with header text
	 */
	HeaderTemplate(line, _) {
		if (line.operation && line.text != line.operation) {
			let text = line.text.split(",");
			text = text.map(t => this.Local.getField(t).value).join(", ");
			return `${text} <span class="webix_pivot_operation">${_(
				line.operation
			)}</span>`;
		} else return this.Local.fixMath(line.text);
	}
	/**
	 * Formats a cell value
	 * @param {(number|string)} value - raw value
	 * @returns {(number|string)} formatted value
	 */
	CellFormat(value) {
		if (!value) value = value === 0 ? "0" : "";

		return value ? parseFloat(value).toFixed(3) : value;
	}
}
